            :width="parcel.width"
            :height="parcel.height"
            stroke="#555"
            :fill="parcelColors[parcel.id] || '#eee'"
          />
        </a>
        <rect
          v-if="selectedParcel"
          class="selected-parcel"
          :x="selectedParcel.coordinateX"
