    border: 1px solid #ccc;
    width: 100%;
  }

  .map-svg--unmatched-hide .parcel--hidden-by-filter {
    display: none;
  }
  .map-svg--unmatched-outline .parcel--hidden-by-filter {
    stroke: #ddd;
    fill: white;
  }
</style>
